.bookPageWrapper {
    display:flex
}


.bookCoverWrapper{
    width: 40%;
    background-color: rgba(227, 227, 227, 0.832);
    padding: 40px;
    min-width: 250px;
}
.bookCover{
    margin: 0 auto;
    width: auto;
    min-width: 150px;
}

.bookInfoWrapper{
    width: 60%;
    text-align: left;
    padding:40px
}

.bookCategory {
    color: rgb(129, 129, 129);
    font-size: 16px;
    margin-bottom: 40px;
}

.bookTitle{
    font-weight: bold;
    color:black;
    font-size: 24px;
    margin-bottom: 20px;

}
.bookPublisher {
    color: rgb(129, 129, 129);
    text-decoration: underline;
    font-size: 16px;
    margin-bottom: 30px;
}
.bookDescription {
    font-weight: 500;
    color: black;
    min-height: 50px;
    border: 1px solid black;
    font-size: 16px;
    padding: 15px
}